<template>
  <!--加载页-->
  <div id="index">
    <router-view></router-view>
  </div>
</template>

<script>

  export default {
    beforeRouteEnter(to, from, next) {
      next(vm => vm.setPath(from.name, to.name))
    },
    methods: {
      setPath(goback, goto) {
        if (goto == 'index') {
          goto = goback;
        }
        var memberId = window.localStorage.getItem('memberId');
        if (!memberId) {
          this.$router.push({path: '/login', query: {goback: goback, goto: goto}})
        }
      }
    }
  }
</script>

<style lang="scss" scoped>
  #index {
    width: 100%;
    float: left;
  }

</style>



